import { useCallback, useEffect, useState } from "react";
import { Button, Popconfirm } from 'antd';
import { StoredManageService } from "../../../services/adminService";
import { Fn } from "../../../helper/function";
import { MyButton } from "../../core/MyButton";
import ManageLinkStoredModifyComponent from "./Link_ModifyComponent";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import TableComponent from "../../core/TableNewComponent";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import { MyBox } from "../../core/MyBox";

export default function ManageLinkStoredIndexComponent() {
    const column = [
        new TableColumn({ title: "STT", key: "stt" }),
        new TableColumn({ title: "Stored nguồn", key: "storedOrigin" }),
        new TableColumn({ title: "Stored liên kết", key: "storedDest" }),
        new TableColumn({
            title: "Hành động", key: "action", render: (data: string, row: any) => {
                return (
                    <>
                        <Button
                            type="link"
                            onClick={() => {
                                handleEdit(row.key);
                            }}>
                            Chỉnh sửa
                        </Button>
                        <Popconfirm
                            title="Bạn có muốn xóa không?"
                            onConfirm={() => {
                                handleDelete(row.key);
                            }}>
                            <Button type="link">Xóa</Button>
                        </Popconfirm>
                    </>
                )
            }
        }),
    ];

    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());

    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        StoredManageService.GetLinkStored(undefined, paginationRequest, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            let row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    stt: index + 1,
                    key: data.key,
                    storedOrigin: data.storedOrigin.name,
                    storedDest: data.storedDest.name,
                }
            });
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        });
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleEdit(key: string) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    function handleDelete(key: string) {
        setIsLoading(true);
        StoredManageService.GetDetailLinkStored(key, (data: any) => {
            StoredManageService.DeleteLinkStored(key, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }
    return (
        <MyBox
            title="Liên kết stored"
            buttonList={() => (
                <MyButton type="primary" value="Thêm mới" onClick={handleClickAdd} />
            )}>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />

            <ManageLinkStoredModifyComponent
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ManageLinkStoredModifyComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}