import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { StoredManageService } from "../../../services/adminService";
import { MyButton } from "../../core/MyButton";
import SearchComponent from "../../core/SearchComponent";

export default function ManageLinkStoredModifyComponent(props: any) {
    const [storedOrigin, setStoredOrigin] = useState('');
    const [storedOriginKey, setStoredOriginKey] = useState('');
    const [storedDest, setStoredDest] = useState('');
    const [storedDestKey, setStoredDestKey] = useState('');

    const [dataSearchOrigin, setDataSearchOrigin] = useState([]);
    const [dataSearchDest, setDataSearchDest] = useState([]);
    const { setIsLoading } = props;

    const loadData = useCallback(() => {
        setIsLoading(true);
        StoredManageService.GetDetailLinkStored(props.keyData, (data: any) => {
            let row = data.data.data;
            setStoredOrigin(row.storedOrigin.name);
            setStoredOriginKey(row.storedOrigin.key);
            setStoredDest(row.storedDest.name);
            setStoredDestKey(row.storedDest.key);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [props.keyData, setIsLoading]);

    useEffect(() => {
        if (props.keyData === undefined) {
            return;
        }
        loadData();
    }, [loadData, props.keyData]);

    function onCloseDrawer() {
        props.setVisible(false);
        if (props.keyData !== undefined) {
            props.setKeyData(undefined);
        }
    }

    function handleSubmitForm() {
        var body = {
            storedOriginKey: storedOriginKey,
            storedDestKey: storedDestKey
        };
        props.setIsLoading(true);

        if (props.keyData === undefined) {
            add(body);
        } else {
            edit(body);
        }
    }

    function add(body: any) {
        StoredManageService.AddLinkStored(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(body: any) {
        StoredManageService.GetDetailLinkStored(props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            StoredManageService.EditLinkStored(props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleSearchStoredOrigin(params: any) {
        props.setIsLoading(true);
        StoredManageService.SearchName(params, (data: any) => {
            setDataSearchOrigin(data.data.data);
        }, undefined, () => {
            props.setIsLoading(false);
        })
    }

    function handleSearchStoredDest(params: any) {
        props.setIsLoading(true);
        StoredManageService.SearchName(params, (data: any) => {
            setDataSearchDest(data.data.data);
        }, undefined, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <div>
            <Drawer
                title={props.keyData === undefined ? "Thêm mới liên kết" : "Chỉnh sửa liên kết"}
                open={props.visible}
                onClose={onCloseDrawer}>
                <Form layout="vertical">
                    <SearchComponent
                        label="Stored nguồn"
                        onSearch={handleSearchStoredOrigin}
                        dataSearch={dataSearchOrigin}
                        resultKey={storedOriginKey}
                        resultName={storedOrigin}
                        setResultKey={setStoredOriginKey}
                        setResultName={setStoredOrigin}
                        isLoading={props.isLoading} />

                    <SearchComponent
                        label="Stored đích"
                        onSearch={handleSearchStoredDest}
                        dataSearch={dataSearchDest}
                        resultKey={storedDestKey}
                        resultName={storedDest}
                        setResultKey={setStoredDestKey}
                        setResultName={setStoredDest}
                        isLoading={props.isLoading} />


                    <MyButton
                        value={props.keyData === undefined ? "Thêm" : "Chỉnh sửa"}
                        onClick={handleSubmitForm}
                        type="success" />
                </Form>
            </Drawer>
        </div>
    );
}